.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 24px;
    margin-left:10px;
    margin-right:10px;
}
.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    transition: .4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 30px;
    width: 30px;
    margin-left:0px;
    margin-top:-3px;
    background-color: #E3E6E8;
    transition: .4s;
}
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #2196F3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
    -webkit-transform: translateX(29px);
    -ms-transform: translateX(29px);
    transform: translateX(29px);
}
input{
    width:60px;
}